* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@200;300;400;500;600;700;800&display=swap');

body {
    font-family: 'Roboto Condensed', sans-serif;
}

form {
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    gap: 1rem;

}


form input,
form button {
    width: 80%;
    margin-top: 20px;
    padding: 12px;
    font-size: 18px;
    border-radius: 8px;
    border: lid gray;
    border: none;
    box-shadow: 0 0 8px gray;
    outline: none;
  
}

form button {
    width: 20%;
    cursor: pointer;
}

.list {
    max-width: 800px;
    /* border: 1px solid black; */
    margin: 0 auto;
    /* background-color: rgba(0, 0, 0, 0.349);
    color: white; */
    border: none;
}

li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 8px gray;
    padding: 1rem;
    border-radius: 8px;
    margin: 10px 0;
    gap: 1rem;
    border: none;
    border-radius: 20px;
    font-size: 18px; 

}

li .content {
    font-family: Arial, Helvetica, sans-serif;
}

span.active{
    text-decoration: line-through;
    color: gray;
    font-size: 15px; 
}

.item button {
    padding: 8px 16px;
    border: none;
    box-shadow: 0 0 8px gray;
    border-radius: 10px;
    margin-left: 10px;
    cursor: pointer;
}

